@import '../scss/variables';

a {
  color: $primary-1;
  &:hover {
    color: #6259ca;
  }
}
.bg-primary {
  background: #6259ca !important;
}
a.bg-primary {
  &:hover,
  &:focus {
    background-color: #5a51c7 !important;
  }
}
button.bg-primary {
  &:hover,
  &:focus {
    background-color: #5a51c7 !important;
  }
}
/*--- gradient-backgrounds --*/

.bg-primary-gradient {
  background: $primary-gradient-1 !important;
}
a.bg-primary-gradient {
  &:hover,
  &:focus {
    background-color: $primary-1 !important;
  }
}
button.bg-primary-gradient {
  &:hover,
  &:focus {
    background-color: $primary-1 !important;
  }
}
.border-primary {
  border-color: #8580c1 !important;
}
.text-primary {
  color: $primary-1 !important;
}
a.text-primary {
  &:hover,
  &:focus {
    color: #4e12b6 !important;
  }
}
.table-primary {
  background-color: #d2cdf9;
  > {
    th,
    td {
      background-color: #d2cdf9;
    }
  }
}
.table-hover .table-primary:hover {
  background-color: #b7cded;
  > {
    td,
    th {
      background-color: #b7cded;
    }
  }
}
.select2-container--default {
  &.select2-container--focus .select2-selection--multiple {
    border-color: $primary-1;
  }
  .select2-selection--multiple .select2-selection__choice {
    background-color: $primary-1 !important;
    border: 1px solid $primary-1 !important;
  }
}
.btn-primary {
  color: #fff !important;
  background: $primary-1 !important;
  border-color: #6a62cc !important;
  &:hover {
    color: #fff;
    background-color: #5a51c7;
    border-color: #5a51c7;
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.9);
  }
  &.disabled,
  &:disabled {
    color: #fff;
    background-color: #5a51c7;
    border-color: #5a51c7;
  }
  &:not(:disabled):not(.disabled) {
    &:active,
    &.active {
      color: #fff;
      background-color: #5a51c7;
      border-color: #5a51c7;
    }
  }
}
.show > .btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #5a51c7;
  border-color: #5a51c7;
}
.btn-primary:not(:disabled):not(.disabled) {
  &:active:focus,
  &.active:focus {
    box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.9);
  }
}
.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.9);
}
.btn-primary-light {
  color: $primary-1;
  background: #d6d2fb !important;
  border-color: #d6d2fb !important;
  &:hover {
    color: #fff;
    background-color: #5a51c7 !important;
    border-color: #5a51c7;
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.9);
  }
}
.btn-primary-gradient {
  color: #fff;
  background: $primary-gradient-1 !important;
  border-color: $primary-1 !important;
  box-shadow: 0 5px 10px rgba(98, 89, 202, 0.3);
  &:hover {
    color: #fff;
    background-color: $primary-1;
    border-color: $primary-1;
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.9);
  }
  &.disabled,
  &:disabled {
    color: #fff;
    background-color: $primary-1;
    border-color: $primary-1;
  }
  &:not(:disabled):not(.disabled) {
    &:active,
    &.active {
      color: #fff;
      background-color: $primary-1;
      border-color: $primary-1;
    }
  }
}
.show > .btn-primary-gradient.dropdown-toggle {
  color: #fff;
  background-color: $primary-1;
  border-color: $primary-1;
}
.btn-primary-gradient:not(:disabled):not(.disabled) {
  &:active:focus,
  &.active:focus {
    box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.9);
  }
}
.show > .btn-primary-gradient.dropdown-toggle:focus {
  box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.9);
}
.btn-outline-primary {
  color: $primary-1;
  background: transparent;
  background-image: none;
  border-color: $primary-1;
  &:hover {
    color: #fff;
    background: #5a51c7;
    border-color: #5a51c7;
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.1);
  }
  &.disabled,
  &:disabled {
    color: $primary-1;
    background-color: transparent;
  }
  &:not(:disabled):not(.disabled) {
    &:active,
    &.active {
      color: #fff;
      background-color: $primary-1;
      border-color: $primary-1;
    }
  }
}
.show > .btn-outline-primary.dropdown-toggle {
  color: #fff;
  background-color: $primary-1;
  border-color: $primary-1;
}
.btn-outline-primary:not(:disabled):not(.disabled) {
  &:active:focus,
  &.active:focus {
    box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.1);
  }
}
.show > .btn-outline-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.1);
}
.btn-link {
  color: $primary-1;
  &:hover {
    color: #5a51c7;
  }
}
.drop-icon-wrap .drop-icon-item:hover {
  color: $primary-1;
}
.dropdown-item {
  &:hover,
  &:focus,
  &.active,
  &:active {
    color: $primary-1;
  }
}
.timeline__item:after {
  border: 6px solid $primary-1;
}
.custom-control-input:checked ~ .custom-control-label::before {
  color: #fff;
  background-color: $primary-1;
  border-color: $primary-1;
}
.custom-checkbox .custom-control-input {
  &:checked ~ .custom-control-label::before,
  &:indeterminate ~ .custom-control-label::before,
  &:disabled:checked ~ .custom-control-label::before {
    background-color: $primary-1;
  }
}
.custom-radio .custom-control-input {
  &:checked ~ .custom-control-label::before {
    background-color: $primary-1;
    border-color: $primary-1;
  }
  &:disabled:checked ~ .custom-control-label::before {
    background-color: $primary-1;
  }
}
.form-select:focus,
.form-file-input:focus ~ .form-file-label::after {
  border-color: none;
}
.form-file-label::after {
  background-color: $primary-1;
}
.custom-range {
  &::-webkit-slider-thumb,
  &::-moz-range-thumb,
  &::-ms-thumb {
    background-color: $primary-1;
  }
}
.nav-pills {
  .nav-link.active,
  .show > .nav-link {
    background-color: $primary-1;
  }
}
.page-link:hover {
  color: #5a51c7;
}
.page-item.active .page-link {
  color: #fff;
  background-color: $primary-1;
  border-color: $primary-1;
}
.range.range-primary {
  input[type='range'] {
    &::-webkit-slider-thumb,
    &::-moz-slider-thumb {
      background-color: $primary-1;
    }
  }
  output {
    background-color: $primary-1;
  }
  input[type='range'] {
    outline-color: $primary-1;
  }
}
.panel.price > .panel-heading {
  background: $primary-1;
}
.nav-tabs .nav-item1 .nav-link {
  &:hover:not(.disabled),
  &.active {
    background: $primary-1;
  }
}
.pop-primary {
  color: $primary-1;
}
.heading-primary {
  background-color: $primary-1;
  color: #ffffff;
  padding: 5px;
}
.breadcrumb-item.active {
  color: #f5f3f9;
}
.breadcrumb-item1 a {
  color: #495584;
}
.breadcrumb-item2 a {
  color: #495584;
}

.panel-title1 a {
  background: #f2f1f9 !important;
}
.btn.dropdown-toggle.btn-primary ~ .dropdown-menu .dropdown-plus-title {
  border-color: rgb(98, 89, 202) !important;
}
.panel-default > .panel-heading {
  color: $primary-1;
}
.alert-primary {
  color: $primary-1;
  background-color: rgba(98, 89, 202, 0.1);
  border-color: rgba(98, 89, 202, 0.01);
  hr {
    border-top-color: $primary-1;
  }
  .alert-link {
    color: $primary-1;
  }
}
.list-group-item-primary {
  color: $primary-1;
  background-color: #cbdbf2;
  &.list-group-item-action {
    &:hover,
    &:focus {
      color: $primary-1;
      background-color: #b7cded;
    }
    &.active {
      color: #fff;
      background-color: $primary-1;
      border-color: $primary-1;
    }
  }
}
.header-bg {
  background: $primary-1;
  /* fallback for old browsers */
  background: -webkit-$primary-gradient-1 !important;
  /* Chrome 10-25, Safari 5.1-6 */
  background: $primary-gradient-1 !important;
}
.footer a:not(.btn) {
  color: $primary-1;
}
.nav-tabs .nav-link {
  &:hover:not(.disabled),
  &.active {
    background: $primary-1;
  }
}
@media (max-width: 992px) {
  .nav-item.with-sub .sub-item {
    border-top: 2px solid $primary-1;
  }
}
.expanel-primary > .expanel-heading {
  color: #fff !important;
  background-color: $primary-1 !important;
  border-color: $primary-1 !important;
}
.login-img {
  background: $primary-1;
}
.avatar {
  background: #e3e1f5 no-repeat center/cover;
}
.spinner,
.spinner-lg,
.double-bounce1,
.double-bounce2,
.cube1,
.cube2 {
  background-color: $primary-1;
}
.lds-heart div {
  background: $primary-1;
  &:after,
  &:before {
    background: $primary-1;
  }
}
.lds-ring div {
  border: 6px solid $primary-1;
  border-color: $primary-1 transparent transparent transparent;
}
.lds-hourglass:after {
  border: 26px solid $primary-1;
  border-color: $primary-1 transparent;
}
a.chip:hover {
  background-color: $primary-1;
}
.tag-primary {
  background-color: $primary-1;
  color: #fff;
}
.selectgroup-input {
  &:checked + .selectgroup-button {
    border-color: $primary-1;
    z-index: 1;
    color: $primary-1;
    background: #f6f4fb;
  }
  &:focus + .selectgroup-button {
    border-color: $primary-1;
    z-index: 2;
    color: $primary-1;
    box-shadow: 0 0 0 2px rgba(98, 89, 202, 0.25);
  }
}
.custom-switch-input:checked ~ .custom-switch-indicator {
  background: $primary-1;
}
.ui-datepicker .ui-datepicker-title {
  color: $primary-1;
}
.timeline--horizontal .timeline-divider {
  background: $primary-1 !important;
}
#back-to-top {
  background-image: $primary-gradient-1;
  &:hover {
    color: $primary-1 !important;
    border: 2px solid $primary-1;
  }
}
.tabs-menu1 ul li .active {
  border-bottom: 3px solid $primary-1;
}
.tab-content i,
.tabs-menu2 ul li .active {
  color: $primary-1;
}
.tab-content .btn i {
  color: $white;
}
.rating-stars .rating-stars-container .rating-star {
  &.is--active .fa-heart,
  &.is--hover .fa-heart {
