  }
}
.message-feed:not(.right) .mf-content {
  background: $primary-1;
  color: #fff;
  &:before {
    border-right-color: $primary-1;
  }
}
.msb-reply button {
  background: $primary-1;
}
/* --------Added--------- css*/

.wizard-card .moving-tab {
  margin-top: 5px;
  background: $primary-gradient-1;
}
.cal1 .clndr {
