    }
  }
}
.footer-payments a:hover {
  color: $primary-1;
}
.footer-social-list a {
  color: $primary-1;
  &:hover {
    color: $primary-1;
  }
}
.sw-theme-dots .nav-tabs .nav-link:hover:not(.disabled) {
  color: $primary-1;
}
.custom-switch-input:checked ~ .custom-switch-indicator {
  background: $primary-1;
}
.label-primary {
  background: $primary-1;
  color: #fff;
}
.bg-primary-transparent {
  background-color: rgba(98, 89, 202, 0.1);
}
.text-primary-shadow {
  text-shadow: 0 5px 10px rgba(98, 89, 202, 0.3);
}
.chart-dropshadow-primary {
  -webkit-filter: drop-shadow((-6px) 12px 4px rgba(98, 89, 202, 0.2));
  filter: drop-shadow((-6px) 12px 4px rgba(98, 89, 202, 0.2));
}
.fc-toolbar {
  .fc-state-active,
  .ui-state-active {
    background: #544bb9;
  }
}
/*-- Sidemenu --*/

.side-menu__item {
  &.active {
    color: #ffffff;
  }
  &.active .side-menu__icon {
    color: #ffffff !important;
  }
}

/*-- Horizontal-menu --*/

.hor-menu .horizontalMenu > .horizontalMenu-list > li > a {
  &.active,
  &:hover {
    color: $primary-1;
  }
}
.horizontalMenu > .horizontalMenu-list > li > {
  ul.sub-menu > li > {
    a:hover,
    ul.sub-menu > li > a:hover {
      color: $primary-1;
    }
  }
  .horizontal-megamenu .link-list li a:hover {
    color: $primary-1;
  }
}
.onoffswitch-checkbox:checked + .onoffswitch-label {
  background-color: $primary-1;
  border-color: $primary-1;
  &:before {
    border-color: $primary-1;
  }
}
.onoffswitch2-checkbox:checked + {
  .onoffswitch2-label {
    background-color: $primary-1;
  }
  .onoffswitch-label2,
  .onoffswitch2-label:before {
    border-color: $primary-1;
  }
}
/*--Dark-mode---*/

.hor-menu .horizontalMenu > .horizontalMenu-list > li > a {
  &.active,
  &:hover {
    color: #6259ca;
  }
}
.dark-mode {
  .breadcrumb-item.active,
  .dropdown-item:hover {
    color: #827ae0;
  }
  .alert-primary {
    color: #827ae0;
  }
}
.dropdown-item:focus,
.dark-mode .dropdown-item.active,
.dropdown-item:active {
  color: #8061ce;
}
.dark-mode {
  .horizontalMenu > .horizontalMenu-list > li > {
    ul.sub-menu > li > {
      a:hover,
      ul.sub-menu > li > a:hover {
        color: #827ae0;
      }
    }
    .horizontal-megamenu .link-list li a:hover {
      color: #827ae0;
    }
  }
  .side-menu__item {
    &.active,
    &:hover,
    &:focus,
    &.active .side-menu__icon {
      color: #dedefd !important;
    }
  }
  .side-menu__item.active {
    color: #ffffff !important;
  }
  .side-menu__item {
    &:hover,
    &:focus {
      color: #827ae0;
    }
  }
  &.dark-menu .side-menu__item {
    &.active .side-menu__icon,
    &:hover .side-menu__icon,
    &:focus .side-menu__icon {
      color: #827ae0;
    }
  }
  .footer a:not(.btn) {
    color: #827ae0;
  }
  .text-primary {
    color: #827ae0 !important;
  }
  a.text-primary:hover,
  a.text-primary:focus {
    color: #746ae8 !important;
  }
  .panel-default > .panel-heading,
  .panel-tabs a {
    color: #ffffff;
  }
  .sidebar-icon {
    color: #827ae0 !important;
  }
}

.dark-mode .side-menu__item {
  &.active {
    color: #ffffff;
  }
  &.active .side-menu__icon {
    color: #ffffff;
  }
}
/*Color-Header*/

.color-hor-header .header.hor-header {
  background: $primary-1;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.light-hor-menu .hor-menu .horizontalMenu > .horizontalMenu-list > li > a {
  &.active,
  &:hover {
    color: #fff;
    background: $primary-1;
  }
}
.dark-hor-menu .hor-menu .horizontalMenu > .horizontalMenu-list > li > a {
  &.active,
  &:hover {
    color: #fff;
    background: $primary-1;
  }
}
.light-menu {
  &.dark-mode .side-menu__item {
    &.active .side-menu__icon,
    &:hover .side-menu__icon,
    &:focus .side-menu__icon,
    &.active,
    &:hover,
    &:focus {
      color: #8061ce;
    }
  }
  .side-menu__item {
    &.active .side-menu__icon {
      color: #fff;
    }
    &:hover .side-menu__icon,
    &:focus .side-menu__icon,
    &.active,
    &:hover,
    &:focus {
      color: $primary-1 !important;
    }
  }
}
.default-body .app-sidebar {
  background: $primary-1 !important;
}
.transparent-mode .side-menu__item {
  &.active .side-menu__icon,
  &:hover .side-menu__icon,
  &:focus .side-menu__icon,
  &.active,
  &:hover,
  &:focus {
    color: $primary-1;
  }
}
/*Color-Header*/

.color-hor-header .header.hor-header {
  background: $primary-1;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.horizontal-main2.horizontal-main {
  background: $primary-1;
}
